  thead{
    th:last-child{
      border-right: 0;
    }
    th{
      // text-align: left;
      padding: 20px 24px;
      color: #1C89C1;
      border-right: 1px solid #1C89C1;
      
    }
    background: rgba(28, 137, 193, 0.10);
  }
  tbody{
    td{
      padding: 15px 24px;
    }
    tr:nth-child(even){
      background: #F9F9F9;
    }
  }
  tfoot{
    background: rgba(23, 64, 108, 0.10);
    td{
      padding: 15px 24px;
    }
  }

}

