.step-animaiton{
  display: inline-block;
  background: hsla(0, 0%, 100%, .41);
  border-radius: 50%;
  height: 20px;
  left: 50%;
  // margin-left: -93px;
  // position: absolute;
  top: -36px;
  width: 20px;
  z-index: 3;
  // animation: stepAnimaiton 2s infinite;
}

.line1{
  animation: stepAnimaiton 2s infinite;
}

.line2{
  animation: stepAnimaiton 2s infinite;
}

.player__video{
  height: 200px;
}
.animation-play{
  cursor: pointer;
  margin-top: 40px;
  position: relative;
  transform: translateY(50px);

  svg{
    margin-left: -52px;
    position: absolute;
    top: 5px;
    z-index: 4;
  }

  .animate-1st{
    animation: pulse 2s infinite;
    background: hsla(0, 0%, 36%, 0.6);
    border-radius: 50%;
    height: 144px;
    left: 50%; 
    margin-left: -72px;
    position: absolute;
    top: -15px;
    width: 144px;
    z-index: 2;
  }
  .animate-2nd{
    animation: pulse 2s infinite;
    background: hsla(0, 0%, 22%, 0.41);
    border-radius: 50%; 
    height: 184px;
    left: 50%;
    margin-left: -93px;
    position: absolute;
    top: -36px;
    width: 184px;
    z-index: 3;
  }
}

