@keyframes pulse{
  0% {
    box-shadow: 0 0 0 0 hsla(0, 0%, 46%, 0.7);
    transform: scale(.95);
  }
  70% {
      box-shadow: 0 0 0 10px hsla(0, 0%, 41%, 0);
      transform: scale(1);
  }
  100% {
      box-shadow: 0 0 0 0 hsla(0, 0%, 26%, 0);
      transform: scale(.95);
  }
}

