.ant-steps-item-icon{
  width: 50px !important;
  height: 50px !important;
  font-size: 22px !important;
  line-height: 44px !important;
  border-color: transparent !important;
}
.ant-steps-item-process{
  .ant-steps-item-icon{
    background-color: #6ad0dc !important;
  }
}
.ant-steps-item-finish{
  .ant-steps-item-icon{
    color: #fff !important;
    background: #6ad0dc !important;
    svg{
      fill: #fff;
    }

  }
}

.ant-steps-item-tail {
  margin-inline-start: 65px !important;
  padding: 14px 24px !important;
}


.ant-steps-item{
  .ant-steps-icon{
    top: 1px !important;
  }
  .ant-steps-item-tail{
    &::after{
      height: 2px !important;
    }
  }
}
.ant-steps-item-finish{
  .ant-steps-item-tail{
    padding-left: 100px;
    overflow: hidden;
    &::before{
      content: "";
      position: absolute;
      left: -50%;
      height: 2px;
      width: 10%;
      margin-left: 150px;
      background-color: #d6273e;
      -webkit-animation: lineAnim 2s linear infinite;
      -moz-animation: lineAnim 2s linear infinite;
      animation: lineAnim 2s linear infinite;
      -webkit-border-radius: 20px;
      -moz-border-radius: 20px;
      border-radius: 20px;
    
    }
    &::after{
      background-color: #6ad0dc !important;

    }
  }
}

@keyframes lineAnim {
  0% {
    left: -40%;
    
  }
  50% {
    left: 20%;
    width: 80%;
  }
  100% {
    left: 100%;
    width: 100%;
  }
}


.lds-ripple {
  /* change color here */
  color: #d6273e;
}
.lds-ripple,
.lds-ripple div {
  box-sizing: border-box;
}
.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid currentColor;
  opacity: 1;
  border-radius: 50%;


  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;


// opacity: 0;

}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 19px;
    left: 21px;
    width: 8px;
    height: 8px;
    }
  4.9% {
    top: 19px;
    left: 21px;
    width: 8px;
    height: 8px;
    opacity: 0;
  }
  5% {
    top: 19px;
    left: 21px;
    width: 8px;
    height: 8px;
    opacity: 1;
  }
  100% {
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    opacity: 0;
  }
}

