html{
  scroll-behavior: smooth;
}

:root {
  --ReactInputVerificationCode-itemWidth: 50px !important;
  --ReactInputVerificationCode-itemHeight: 50px !important;
  --ReactInputVerificationCode-itemSpacing: 10px !important;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* background-color: #f6f8fb; */

  /* background: rgb(246,248,251);
  background: linear-gradient(297deg, rgba(246,248,251,1) 21%, rgba(38,120,217,0.1) 49%, rgba(246,248,251,1) 66%); */
/* 
background: rgb(246,248,251);
background: linear-gradient(137deg, rgba(246,248,251,1) 76%, rgba(38,120,217,0.15) 99%, rgba(246,248,251,1) 100%); */

/* background: rgb(246,248,251);
background: linear-gradient(137deg, rgba(246,248,251,1) 71%, rgba(38,120,217,0.15) 87%, rgba(246,248,251,1) 100%); */

  background: white;
  
  font-family: 'Galano Grotesque', sans-serif;
  font-size: 18px;
}

*{
  font-family: 'Galano Grotesque', sans-serif;
  font-weight: 400;
  line-height: 1.3;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

@media only screen and (max-width: 576px) {
  .container{
    max-width: 95%;
  }
}

@media only screen and (max-width: 768px) {
  :root {
    --ReactInputVerificationCode-itemWidth: 40px !important;
    --ReactInputVerificationCode-itemHeight: 40px !important;
    --ReactInputVerificationCode-itemSpacing: 5px !important;
  }
}

.verification_code_wrapper > div{
  margin: 0 auto;
}

.verification_code_wrapper div div{
  background-color: #f6f8fb;
  font-weight: 500;
  border-radius: 8px;
  line-height: 38px;
} 


.verification_code_wrapper .is-active{
  box-shadow: inset 0 0 0 2px #1c89c1 !important;
}


.input-mask-custom-style{
  box-sizing: border-box !important;
  margin: 0 !important;
  list-style: none !important;
  /* font-family: 'Galano Grotesque', sans-serif; */
  position: relative !important;
  display: inline-block !important;
  width: 100% !important;
  min-width: 0 !important;
  /* background-color: #ffffff !important; */
  background-image: none !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: #d9d9d9 !important;
  transition: all 0.2s !important;
  /* font-weight: 400 !important; */
  padding: 7px 11px !important;
  /* font-size: 16px !important; */
  line-height: 1.5 !important;
  border-radius: 8px !important;
}

.input-mask-custom-style:focus, .input-mask-custom-style:focus-within {
  border-color: #144176 !important;
  box-shadow: 0 0 0 2px rgba(5, 40, 25, 0.46) !important;
  outline: 0 !important;
}

.selected-option-bg{
  background-color: rgba(0, 122, 94, 0.05);
}
.test-numbers-border {
  border-color: rgba(0, 122, 94, 0.5);
}

.ant-radio-button-wrapper{
  height: auto !important;
}

.card-shadow{
  box-shadow: 0 4px 20px rgba(159, 171, 185, .16);
}

.solid-shadow{
  box-shadow: 0px 0px 50px 0px rgba(7, 21, 38, 0.06);
}


@layer components {
  .main-card {
    border: 1px solid #ECECEC;
    box-shadow: 0px 0px 50px 0px rgba(7, 21, 38, 0.06);
    @apply md:p-[30px] p-[14px] rounded-xl bg-white mb-8  max-sm:mx-auto;
  }
  .mainTextColor{
    color: #070F18;
  }
}