.loader-main {
  width : 150px;
  height: 150px;
  border: 4px dotted;
  border-color: transparent;
  border-radius: 50%;
  display: inline-block;
  animation: rotation 2.5s ease infinite;
}
.loader-main > svg {
  display: block;
  animation: no-spin 2.5s ease infinite;
}

@keyframes no-spin {
  0% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes rotation {
  0% { transform: rotate(0deg) }
  100% { transform: rotate(360deg) }
}

@media only screen and (max-width: 768px) {
  .loader-main {
    width : 80px;
    height: 80px;
    border: 2px dotted;
    border-color: transparent;
    border-radius: 50%;
    display: inline-block;
    animation: rotation 2.5s ease infinite;
  }
}